// common styles for navigation action dropdowns
%navigationActionDropdown {
  left: unset;
  right: 0;

  // remove margins from children
  * {
    margin: 0;
  }

  > * {
    // add margin to icons
    span:first-of-type:not(:only-of-type) {
      align-self: flex-start;
      margin: 0 var(--spacing-2-xs) 0 calc(var(--spacing-3-xs) * -1);
    }
  }
}

%dropDownItemMenuBase {
  // remove margins from children
  * {
    margin: 0;
  }

  // dropdown menu item
  > * {
    font-weight: normal;

    // dropdown menu item icon
    span:first-of-type:not(:only-of-type) {
      align-self: flex-start;
      margin: 0 var(--spacing-2-xs) 0 calc(var(--spacing-3-xs) * -1);
    }
  }

  @media small-down {
    --menu-border-width: 0;
    --menu-width: calc(100% - (var(--spacing-xl) + var(--header-focus-outline-width)));
    --menu-item-background-color: transparent;
    --menu-item-background-color-highlighted: transparent;
    --menu-item-color: inherit;
    --menu-item-color-highlighted: var(--menu-item-color);
    align-self: stretch;
    margin-left: var(--spacing-l);
    max-width: unset;
    position: relative;
    width: auto;
    // override inline min-width and top styles applied by the MenuButton component
    min-width: auto !important;
    top: 0 !important;

    > * {
      padding-left: var(--spacing-2-xs);

      &:focus {
        box-shadow: 0 0 0 var(--header-focus-outline-width) var(--header-focus-outline-color);
      }
    }
  }
}
